import React from 'react'
import contactimg from './myimage/contact.svg'

export default function Contact(props) {

    return (
        <>
            <div class={`p-3 my-3  rounded-3 text-${props.mode === 'dark' ? 'light' : 'dark'}`}>
                <div class={`container my-4`}>
                    <h4 class={`display-5 fw-bold  text-center mb-5`}>Contact</h4>
                    {/* <p class="col-md-12 fs-4 text-center">If you need anything reguarding this site or to inform something don't hesitate. Please mail us at <b>mail@textutils.net</b></p> */}

                    <img src={contactimg} className="contactImg" alt="" />
                    <p className=' p-0 mt-5 text-center'>Leave a mail to our inbox <b className='text-primary'>info@textutils.web</b> </p>





                </div>
            </div>




        </>
    )
}
