import React from 'react'
import PropTypes from 'prop-types'
import { NavLink } from 'react-router-dom'

export default function Navbar(props) {

    return (
        <nav className={`navbar  navbar-expand-lg navbar-dark   bg-${props.mode === 'dark' ? 'dark' : 'primary'}`}>
            <div className="container-fluid">
                <NavLink className="navbar-brand " to="/">

                    <svg xmlns="./public/icons/lightning-charge-fill.svg" width="22" height="22" fill="currentColor" className="bi bi-lightning-charge-fill" viewBox="0 0 16 16">
                        <path d="M11.251.068a.5.5 0 0 1 .227.58L9.677 6.5H13a.5.5 0 0 1 .364.843l-8 8.5a.5.5 0 0 1-.842-.49L6.323 9.5H3a.5.5 0 0 1-.364-.843l8-8.5a.5.5 0 0 1 .615-.09z" />
                    </svg>

                    {props.title}</NavLink>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                        {/* <li className="nav-item">
                            <NavLink className="nav-link active" aria-current="page" to="/">Home</NavLink>
                        </li> */}
                        <li className="nav-item">
                            <NavLink className="nav-link active" to="/about">{props.about}</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link active" to="/contact">{props.contact}</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link active" to="/privacy">{props.privacy}</NavLink>
                        </li>



                    </ul>
                    {/* Notice  */}

                    {/* <marquee className={`notice   text-${props.mode === 'dark' ? 'warning' : 'light'}`} width="60%" direction="left" >
                        <span class="badge bg-success text-light mx-2">Update</span>
                        <span className='badgecontainer'>New feature available!! Practice typing online just start the timer and get ready</span>

                        <span class="badge bg-warning text-dark mx-2">Warning</span>
                        <span className="badgecontainer"> After timer started don't move to another page of this site error may occar</span>
                    </marquee> */}

                    {/* Notice  */}

                    {/* Dark mode switch */}
                    <div className={`form-check form-switch mx-3 text-light`}  >

                        <input className="form-check-input" onClick={() => {
                            props.toggleMode()
                        }} type="checkbox" role="switch" id="flexSwitchCheckDefault" />
                        <label className="form-check-label " htmlFor="flexSwitchCheckDefault"> {props.mode === 'dark' ? 'Turn off Darkmode' : 'Turn on Darkmode'}</label>
                    </div>
                    {/* Dark mode switch */}


                    {/* Search  */}
                    {/* <form className="d-flex">
                        <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
                        <button className={`btn btn-${props.mode === 'dark' ? 'warning' : 'warning'}`} type="submit">
                            <svg xmlns="./public/" width="16" height="16" fill="currentColor" className="bi bi-search" viewBox="0 0 16 16">
                                <path d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z" />
                            </svg></button>
                    </form> */}
                    {/* Search  */}

                </div>
            </div>
        </nav>
    )
}
Navbar.propTypes = {
    title: PropTypes.string.isRequired,
    about: PropTypes.string.isRequired
}

Navbar.defaultProps = {
    title: 'Change Title',
    about: 'Default About us',
    contact: 'Contact Us',
    privacy: 'Privacy Policy'
};