// import logo from './logo.svg';
import './App.css';
import About from './components/About';
import Contact from './components/Contact';
import Privacy from './components/Privacy';
import Navbar from './components/Navbar';
import TextForm from './components/TextForm';

// import Footer from './components/Footer';
import React, { useState } from 'react'
import {
  BrowserRouter as Router,
  Routes,
  Route
} from 'react-router-dom';



function App() {

  const [mode, setMode] = useState('light'); //Wheather  dark mode is enabled or not
  const toggleMode = () => {
    if (mode === 'light') {
      setMode('dark');
      document.body.style.backgroundColor = 'rgba(12, 11, 11, 0.767)';
      // document.body.style.backgroundImage = URL('/main_bg.svg')
      console.log(mode)
    }
    else {
      setMode('light')
      document.body.style.backgroundColor = 'rgb(241, 243, 247)';
      console.log(mode)
    }

  }
  return (
    <>

      <Router>
        <Navbar title='   Text Utils' mode={mode} about='About' contact='Contact' privacy='Privacy' toggleMode={toggleMode} />

        <div className="container my-3">
          <Routes>

            <Route exact path='/' element={< TextForm heading="Process your Text with One click" mode={mode} />}></Route>
            <Route exact path='/about' element={< About mode={mode} />}></Route>
            <Route exact path='/contact' element={< Contact mode={mode} />}></Route>
            <Route exact path='/privacy' element={< Privacy mode={mode} />}></Route>
          </Routes>
        </div>
        {/* <Footer /> */}
      </Router>
    </>
  );
}


export default App;
