import React, { useState } from 'react';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

toast.configure()


//For using custom alert pressing buttons

export default function TextForm(props) {

    //For using custom alert pressing button

    const notifyUppercase = () => {
        let charCount = text.length;
        if (charCount === 0) {
            wordCount = 0;
        }
        else {
            wordCount = text.split(" ").length;
        }
        if (wordCount > 0) {
            handleUpClick();
            toast.success('Text Converted to Uppercase', { position: toast.POSITION.BOTTOM_RIGHT })
        }
        else {
            toast.warning('Text Not Found!!', { position: toast.POSITION.BOTTOM_RIGHT })
        }

    }
    const notifyLowercase = () => {
        let charCount = text.length;
        if (charCount === 0) {
            wordCount = 0;
        }
        else {
            wordCount = text.split(" ").length;
        }
        if (wordCount > 0) {
            handleLowClick();
            toast.success('Text Converted to Lowercase', { position: toast.POSITION.BOTTOM_RIGHT })
        }
        else {
            toast.warning('Text Not Found!!', { position: toast.POSITION.BOTTOM_RIGHT })
        }


    }
    const notifyCopy = () => {
        let charCount = text.length;
        if (charCount === 0) {
            wordCount = 0;
        }
        else {
            wordCount = text.split(" ").length;
        }
        if (wordCount > 0) {
            handleCopy();
            toast.success('Text Copied to Clipboard', { position: toast.POSITION.BOTTOM_RIGHT })
        }
        else {
            toast.warning('Text Not Found!!', { position: toast.POSITION.BOTTOM_RIGHT })
        }


    }
    const notifyReverse = () => {
        let charCount = text.length;
        if (charCount === 0) {
            wordCount = 0;
        }
        else {
            wordCount = text.split(" ").length;
        }
        if (wordCount > 0) {
            handleReverseText();
            toast.success('Text Reversed Successfully', { position: toast.POSITION.BOTTOM_RIGHT })
        }
        else {
            toast.warning('Text Not Found!!', { position: toast.POSITION.BOTTOM_RIGHT })
        }

    }
    const notifyDownload = () => {
        let charCount = text.length;
        if (charCount === 0) {
            wordCount = 0;
        }
        else {
            wordCount = text.split(" ").length;
        }
        if (wordCount > 0) {
            handleDownload(text);
            toast.success('Downloaded as Text File', { position: toast.POSITION.BOTTOM_RIGHT })
        }
        else {
            toast.warning('Text Not Found!!', { position: toast.POSITION.BOTTOM_RIGHT })
        }

    }
    const notifyExtraSpace = () => {
        let charCount = text.length;
        if (charCount === 0) {
            wordCount = 0;
        }
        else {
            wordCount = text.split(" ").length;
        }
        if (wordCount > 0) {
            handleExtraSpaces();
            toast.success('Extra Space Removed', { position: toast.POSITION.BOTTOM_RIGHT })
        }
        else {
            toast.warning('Text Not Found!!', { position: toast.POSITION.BOTTOM_RIGHT })
        }

    }
    const notifyDelete = () => {
        let charCount = text.length;
        if (charCount === 0) {
            wordCount = 0;
        }
        else {
            wordCount = text.split(" ").length;
        }
        if (wordCount > 0) {
            handleClearClick();
            toast.success('Content Deleted Successfully', { position: toast.POSITION.BOTTOM_RIGHT })
        }
        else {
            toast.warning('Content Not Found!!', { position: toast.POSITION.BOTTOM_RIGHT })
        }
    }
    //For using custom alert pressing button


    let [text, setText] = useState('');


    // Set Timer 


    /*Add new features Here*/
    const handleExtraSpaces = () => {
        let newText = text.split(/[ ]+/);
        setText(newText.join(" "))
        console.log("Remove Extra spaces button Clicked")
    }


    const handleDownload = (text) => {
        var element = document.createElement('a');
        let i = 1000;
        element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(text));
        element.setAttribute('download', `TextUtilDownload${++i}`);
        element.style.display = 'none';
        document.body.appendChild(element);
        element.click();

        document.body.removeChild(element);
    }


    const handleReverseText = () => {
        console.log("Data is reversed but you can correct by reverse again");
        text = text.split("");
        let reverseText = text.reverse().toString().replaceAll(",", "");


        console.log(text.reverse().toString().replaceAll(",", ""));
        setText(reverseText)
    }

    const handleCopy = () => {
        console.log("Copied to Clipboard");
        var text = document.getElementById("myBox");
        text.select();
        navigator.clipboard.writeText(text.value);


    }
    /*Add new features Here*/
    // const handleTimerClick=()=>{

    // }
    const handleClearClick = () => {
        console.log("Clear button Was Clicked" + text);
        let newText = '';
        setText(newText);
    }
    const handleUpClick = () => {
        console.log("UPPERCASE Was Clicked" + text);
        let newText = text.toUpperCase();
        setText(newText);
    }
    const handleLowClick = () => {
        console.log("lowercase Was Clicked" + text);
        let newText = text.toLowerCase();
        setText(newText);
    }
    const handleOnChange = (event) => {
        console.log("On Change");
        setText(event.target.value);
    }

    // Text Summary
    let wordCount;
    let charCount = text.length;
    let Preview = 'Content not found!! Please Type or paste your content';
    if (charCount === 0) {
        wordCount = 0;
    }
    else {
        wordCount = text.split(" ").length;
    }

    // Timer Code Start

    function zeroPadMin(numberStrMin) {
        return numberStrMin.padStart(2, "0");
    }
    function zeroPadSec(numberStrSec) {
        return numberStrSec.padStart(2, "0");
    }


    var myTimer;

    function myTimerClock() {
        toast.success('Timer Enabled for 10 minutes', { position: toast.POSITION.BOTTOM_RIGHT })
        myTimer = setInterval(myClock, 1000);
        var c = 600; //Initially set to 10 Minutes
        function myClock() {
            --c
            var seconds = c % 60; // Seconds that cannot be written in minutes
            var secondsInMinutes = (c - seconds) / 60;
            var minutes = secondsInMinutes % 60; // Minutes that cannot be written in hours
            console.clear();
            // *****************************************
            var numStringMin = minutes.toString();
            var paddedNumMin = zeroPadMin(numStringMin);

            var numStringSec = seconds.toString();
            var paddedNumSec = zeroPadSec(numStringSec);
            // ***************************************
            console.log(paddedNumMin + ":" + paddedNumSec)
            let paddingTime = paddedNumMin + " Min " + paddedNumSec + " Sec left";

            document.getElementById("timerLiveTime").innerHTML = paddingTime
            document.getElementById("timerLiveTime").style.color = 'yellow'

            if (c === 0) {
                clearInterval(myTimer);
            }
        }

    }



    return (

        <>

            <div className='container'>
                <div className={`container text-${props.mode === 'dark' ? 'light' : 'dark'}`}>
                    {/* Top heading */}
                    <h1 className=" mt-4 mb-5 ">{props.heading} </h1>

                    {/* Main text Container */}
                    <div className="mb-4">
                        {/* Timer Code */}
                        <span className={`d-${'flex'}`} id='myDivId'>
                            <span className={`TimerContainer text-light bg-${props.mode === 'dark' ? 'success' : 'primary'} `}>
                                <span>
                                    <span id="timerLiveTime" > <i> <i className='newfeature'>Practice Typing (Set timer)</i>  </i> </span>

                                </span>

                            </span>
                        </span>
                        {/* Timer Code */}
                        <div className={`textbox bg-${props.mode === 'dark' ? 'success' : 'primary'}`}>
                            <textarea className={`text-${props.mode === 'dark' ? 'light' : 'dark'} form-control border-2 border-${props.mode === 'dark' ? 'success' : 'primary'} bg-${props.mode === 'dark' ? 'dark' : 'light'} p-4 py-4`} id="myBox" rows="9" value={text} onChange={handleOnChange}></textarea>
                        </div>

                    </div>
                    {/* Main text Container */}


                </div>
                <div className="container">
                    <button id='btn1' className={`openPopup `} onClick={
                        () => {
                            notifyUppercase()
                        }
                    }>

                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-up" viewBox="0 0 16 16"><path fillRule="evenodd" d="M8 15a.5.5 0 0 0 .5-.5V2.707l3.146 3.147a.5.5 0 0 0 .708-.708l-4-4a.5.5 0 0 0-.708 0l-4 4a.5.5 0 1 0 .708.708L7.5 2.707V14.5a.5.5 0 0 0 .5.5z" />
                        </svg><b>     UPPERCASE</b> </button>

                    <button id='btn2' className={`openPopup `} onClick={() => {
                        notifyLowercase()
                    }}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-down" viewBox="0 0 16 16"><path fillRule="evenodd" d="M8 1a.5.5 0 0 1 .5.5v11.793l3.146-3.147a.5.5 0 0 1 .708.708l-4 4a.5.5 0 0 1-.708 0l-4-4a.5.5 0 0 1 .708-.708L7.5 13.293V1.5A.5.5 0 0 1 8 1z" />
                        </svg><b>      lowercase</b></button>




                    {/* Add new Buttons Here */}

                    <button id='btn3' className={`openPopup `} onClick={
                        () => {
                            notifyCopy()
                        }
                    }>
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-clipboard" viewBox="0 0 16 16">
                            <path d="M4 1.5H3a2 2 0 0 0-2 2V14a2 2 0 0 0 2 2h10a2 2 0 0 0 2-2V3.5a2 2 0 0 0-2-2h-1v1h1a1 1 0 0 1 1 1V14a1 1 0 0 1-1 1H3a1 1 0 0 1-1-1V3.5a1 1 0 0 1 1-1h1v-1z" />
                            <path d="M9.5 1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-3a.5.5 0 0 1-.5-.5v-1a.5.5 0 0 1 .5-.5h3zm-3-1A1.5 1.5 0 0 0 5 1.5v1A1.5 1.5 0 0 0 6.5 4h3A1.5 1.5 0 0 0 11 2.5v-1A1.5 1.5 0 0 0 9.5 0h-3z" />
                        </svg><b>      Copy Text</b></button>

                    <button id='btn4' className={`openPopup `} onClick={
                        () => {
                            notifyReverse()
                        }
                    }>
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-hourglass-split" viewBox="0 0 16 16">
                            <path d="M2.5 15a.5.5 0 1 1 0-1h1v-1a4.5 4.5 0 0 1 2.557-4.06c.29-.139.443-.377.443-.59v-.7c0-.213-.154-.451-.443-.59A4.5 4.5 0 0 1 3.5 3V2h-1a.5.5 0 0 1 0-1h11a.5.5 0 0 1 0 1h-1v1a4.5 4.5 0 0 1-2.557 4.06c-.29.139-.443.377-.443.59v.7c0 .213.154.451.443.59A4.5 4.5 0 0 1 12.5 13v1h1a.5.5 0 0 1 0 1h-11zm2-13v1c0 .537.12 1.045.337 1.5h6.326c.216-.455.337-.963.337-1.5V2h-7zm3 6.35c0 .701-.478 1.236-1.011 1.492A3.5 3.5 0 0 0 4.5 13s.866-1.299 3-1.48V8.35zm1 0v3.17c2.134.181 3 1.48 3 1.48a3.5 3.5 0 0 0-1.989-3.158C8.978 9.586 8.5 9.052 8.5 8.351z" />
                        </svg><b>      Reverse</b></button>

                    <button id='btn5' className={`openPopup `} onClick={() => {
                        notifyDownload()
                    }}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-file-earmark-arrow-down-fill" viewBox="0 0 16 16">
                            <path d="M9.293 0H4a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h8a2 2 0 0 0 2-2V4.707A1 1 0 0 0 13.707 4L10 .293A1 1 0 0 0 9.293 0zM9.5 3.5v-2l3 3h-2a1 1 0 0 1-1-1zm-1 4v3.793l1.146-1.147a.5.5 0 0 1 .708.708l-2 2a.5.5 0 0 1-.708 0l-2-2a.5.5 0 0 1 .708-.708L7.5 11.293V7.5a.5.5 0 0 1 1 0z" />
                        </svg><b>      Download</b></button>



                    <button id='btn6' className={`openPopup `} onClick={() => {
                        notifyExtraSpace()
                    }}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrows-collapse" viewBox="0 0 16 16">
                            <path fillRule="evenodd" d="M1 8a.5.5 0 0 1 .5-.5h13a.5.5 0 0 1 0 1h-13A.5.5 0 0 1 1 8zm7-8a.5.5 0 0 1 .5.5v3.793l1.146-1.147a.5.5 0 0 1 .708.708l-2 2a.5.5 0 0 1-.708 0l-2-2a.5.5 0 1 1 .708-.708L7.5 4.293V.5A.5.5 0 0 1 8 0zm-.5 11.707-1.146 1.147a.5.5 0 0 1-.708-.708l2-2a.5.5 0 0 1 .708 0l2 2a.5.5 0 0 1-.708.708L8.5 11.707V15.5a.5.5 0 0 1-1 0v-3.793z" />
                        </svg><b>       Remove Extra Spaces</b></button>

                    {/* Add new Buttons Here */}

                    {/* // handleClearClick  toggleModal */}
                    <button id='btn7' className={`openPopup `} onClick={() => {
                        notifyDelete()
                    }}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-trash" viewBox="0 0 16 16">
                            <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z" />
                            <path fillRule="evenodd" d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z" />
                        </svg><b>       Clear Field</b></button>
                    {/* Timer Button Start */}
                    <button id='btn8' className={`openPopup `} onClick={() => {
                        myTimerClock()

                    }}>
                        {/* onClick={handleTimerClick} */}
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-alarm" viewBox="0 0 16 16">
                            <path d="M8.5 5.5a.5.5 0 0 0-1 0v3.362l-1.429 2.38a.5.5 0 1 0 .858.515l1.5-2.5A.5.5 0 0 0 8.5 9V5.5z" />
                            <path d="M6.5 0a.5.5 0 0 0 0 1H7v1.07a7.001 7.001 0 0 0-3.273 12.474l-.602.602a.5.5 0 0 0 .707.708l.746-.746A6.97 6.97 0 0 0 8 16a6.97 6.97 0 0 0 3.422-.892l.746.746a.5.5 0 0 0 .707-.708l-.601-.602A7.001 7.001 0 0 0 9 2.07V1h.5a.5.5 0 0 0 0-1h-3zm1.038 3.018a6.093 6.093 0 0 1 .924 0 6 6 0 1 1-.924 0zM0 3.5c0 .753.333 1.429.86 1.887A8.035 8.035 0 0 1 4.387 1.86 2.5 2.5 0 0 0 0 3.5zM13.5 1c-.753 0-1.429.333-1.887.86a8.035 8.035 0 0 1 3.527 3.527A2.5 2.5 0 0 0 13.5 1z" />
                        </svg><b>       Set Timer (10m)</b></button>
                    {/* Timer Button End */}

                    {/* Modal Button */}


                </div>

            </div >
            <div className={`d-${wordCount === 0 ? 'none' : 'flex'}`}>
                <div className=" container 	" >

                    <div className={`container text-${props.mode === 'dark' ? 'light' : 'primary'} my-4`}>
                        <h2 className={`text-${props.mode === 'dark' ? 'light' : 'primary'}`}>Your Text Summary is Here</h2>
                        <p><b>

                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-check-circle-fill" viewBox="0 0 16 16">
                                <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z" />
                            </svg>{"        "}

                            {wordCount}</b> words and <b>{text.length}</b> characters</p>


                        <p>

                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-check-circle-fill" viewBox="0 0 16 16">
                                <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z" />
                            </svg>{"        "}

                            Read within <b >{Math.ceil((0.008 * wordCount))}</b> minutes</p>
                    </div>

                    <div className={`container text-warning my-5`}>
                        <h3 className={`d-flex justify-content-center mb-0 p-2 text-${props.mode === 'light' ? 'light' : 'light'} bg-${props.mode === 'dark' ? 'dark' : 'primary'}`}>Preview</h3>
                        <p className={`text-${charCount > 0 ? 'light' : 'warning'} p-4 bg-secondary`}>{charCount > 0 ? text : Preview}</p>
                    </div>
                </div>
            </div>




        </>

    )

}
